/* Existing styles for overall layout */
:root {
    --grey-input-field: rgba(0, 0, 0, 0.51);
    --variable-collection-purpl: rgba(64, 31, 72, 1);
    --variable-collection-background-light-green: rgba(231, 255, 225, 1);
    --variable-collection-dark-green: rgba(42, 153, 3, 0.61);
    --variable-collection-title-dark-green: rgba(34, 96, 12, 1);
  }
  
  /* Set the background color of the body or a specific container */
  body {
    background-color: var(--variable-collection-background-light-green); /* Apply the variable */
  }

  .title {
    height: 59px;
    width: 441px;
    text-align: right; /* Align the title to the right */
    padding-right: 10px; /* Add some padding on the right */
  }
  
  .title-wrapper {
    color: var(--variable-collection-title-dark-green);
    font-family: "Inter-Bold", Helvetica;
    font-size: 25px;
    font-weight: 700;
    letter-spacing: 0;
    line-height: normal;
    position: relative;
    width: 441px;
    align-items: center;
  }
  
  .frame .frame-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center; /* Centers children horizontally */
    justify-content: center; /* Centers children vertically */
    margin: auto;
    padding: 20px;
    width: 50%; /* Adjust width as needed */
    border-radius: 5px;
  }
    
  /* New styles from Figma design for .property-default */
  .property-default {
    align-items: flex-start;
    background-color: #8ec67a9c;
    border-radius: 4px;
    display: inline-flex;
    flex-direction: column;
    gap: 10px;
    padding: 10px;
    position: relative;
    margin-bottom: 20px; /* Increased spacing between modules */
    width: 90%; /* Ensure full width to respect the parent's centering */
  }
    
  .property-default .frame {
    align-items: flex-start;
    display: inline-flex;
    flex: 0 0 auto;
    flex-direction: column;
    position: relative;
  }
    
  .property-default .text-wrapper {
    color: var(--variable-collection-title-dark-green);
    font-family: "ABeeZee-Regular", Helvetica;
    font-size: 14px;
    font-weight: 400;
    letter-spacing: 0;
    line-height: normal;
    position: relative;
    width: 320px;
  }
    
  .property-default .div-wrapper {
    background-color: #ffffff;
        border-radius: 5px;
        padding: 15px;
        margin-bottom: 15px;
        position: relative;
        box-sizing: border-box; /* Include padding and border in the element's total width */
        width: calc(100% - 20px); /* Adjust width to account for padding */
        align-self: center;
  }
  .property-default .input-text-white-module-wrapper {
    background-color: #ffffff;
        border-radius: 5px;
        padding: 15px;
        margin-bottom: 15px;
        position: relative;
        box-sizing: border-box; /* Include padding and border in the element's total width */
        width: calc(100% - 20px); /* Adjust width to account for padding */
        align-self: center;
  }
  
    
  .property-default .div {
    color: var(--grey-input-field);
    font-family: "ABeeZee-Regular", Helvetica;
    font-size: 14px;
    font-weight: 400;
    letter-spacing: 0;
    line-height: normal;
    position: relative;
    width: 320px;
    margin-right: auto;
    border: none;
  }

  .property-default .input-text {
    color: var(--grey-input-field);
    font-family: "ABeeZee-Regular", Helvetica;
    font-size: 14px;
    font-weight: 400;
    letter-spacing: 0;
    line-height: normal;
    position: relative;
    width: 320px;
    margin-right: auto;
    border: none;
  }
    
  .calculate-button {
    background-color: var(--variable-collection-dark-green);
    border-radius: 4px;
    height: 38px;
    width: 198px;
    border: none;
    padding: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 20px; /* Add some space above the button */
    align-self: center; /* Center the button in the flex container */
  }
  
  .calculate-button-wrapper {
    color: #ffffff;
    font-family: "Roboto-Bold", Helvetica;
    font-size: 15px;
    font-weight: 700;
    letter-spacing: 1.05px;
    line-height: normal;
    text-align: center;
  }
  